*, :after, :before {
  vertical-align: baseline;
  box-sizing: inherit;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  font-style: inherit;
  font-weight: inherit;
  line-height: inherit;
  background-color: #0000;
  border: 0;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

svg {
  color: inherit;
  shape-rendering: geometricPrecision;
  fill: currentColor;
}
